.container5 {
    padding-top: 200px;
    background-color: lightcyan;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container h1 {
    margin: 0;
}
.container button{
    background-color: blue;
    color: white;
    border-radius: 8px;
    padding-inline: 10px;
    padding-top: 6px;
    padding-bottom: 6px;
    transition: 0.5s;
    margin-left: 5px;
}
.container button:hover {
    background-color: aquamarine;
}
.container input {
    padding: 6px;
    border-radius: 8px;

}