.main {
    padding-top: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.darah-heseg{
   
    border: 1px solid black;
    height: 70vh;
    width: 60vw;
    cursor: pointer;
    margin-top: 20px;
    margin-inline: 20px;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 60px;
    transition: 0.1s;
}
.darah-heseg:active{
    background-image: linear-gradient(to top, #4fb576 0%, #44c489 30%, #28a9ae 46%, #28a2b7 59%, #4c7788 71%, #6c4f63 86%, #432c39 100%);
    transform: scale(0.95);
}
.Hairstag{
    background-color: purple;
    width: 19vw;
    height: 200px;
    border-radius: 10px;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
}
.pink{
    background-image: linear-gradient(-20deg, #fc6076 0%, #ff9a44 100%);
}
.orange{
    background-image: linear-gradient(to top, #f43b47 0%, #453a94 100%);
}
.green{
    background-image: linear-gradient(to top, #4fb576 0%, #44c489 30%, #28a9ae 46%, #28a2b7 59%, #4c7788 71%, #6c4f63 86%, #432c39 100%);
}
.Hairstag h1 {
    font-size: 60px;
    font-weight: bold;
    margin: 0;
}
.Hairstag p {
    font-size: 32px;
}
.container4 {
    display: flex;
    justify-content: center;
}
.ads{
    flex-direction: column;
    justify-content: space-around;
}